@import "http://fonts.googleapis.com/css?family=Open+Sans&css";

body {
  background-color: #fff;
  font-family: Open Sans, Arial, Helvetica, sans-serif;
}

body .form {
  width: 90%;
  background-color: #fff;
  border-radius: 5px;
  margin: 10px auto;
  padding: 5px;
  font-family: Open Sans, Arial, Helvetica, sans-serif;
}

body .form .logo__form {
  height: 160px;
  margin: auto;
  display: block;
}

body .form h1 {
  text-align: center;
  font-size: 3em;
}

* {
  margin: 0;
  padding: 0;
}

form {
  padding: 5px;
  font-size: 2em;
}

form input, form select, form textarea {
  resize: none;
  color: #000;
  margin-bottom: 20px;
  display: block;
}

form textarea {
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
  font-family: Open Sans, Arial, Helvetica, sans-serif;
  font-size: 30px;
}

form input {
  box-sizing: border-box;
  width: 100%;
  height: 50px;
  vertical-align: baseline;
  border: none;
  border-bottom: 1px solid;
  outline: none;
  padding: 3px;
  font-family: Open Sans, Arial, Helvetica, sans-serif;
  font-size: 20px;
  transition: border .2s cubic-bezier(.25, .46, .45, .94);
}

form input:focus {
  background-color: #0000000f;
  border-bottom: 1px solid #00f;
}

form input::placeholder {
  margin-bottom: 0;
  padding-bottom: 0;
}

form input[type="date"] {
  height: 30px;
  background-color: #ff0;
  border: 1px solid #000;
}

form input[type="button"], form input[type="reset"] {
  background-color: #fff;
  border: 1px solid;
  border-radius: 2px;
  transition: all .5s;
}

form input[type="button"]:hover, form input[type="reset"]:hover {
  color: #fff;
  background-color: #535353;
  transition: all .1s;
}

form input[type="button"]:focus-visible, form input[type="reset"]:focus-visible {
  color: #fff;
  background-color: #000;
}

form p {
  display: inline;
}

form input[type="radio"] {
  height: 20px;
  width: 50px;
  margin-bottom: 0;
  display: inline;
}

form #itipoOutro {
  width: 100%;
  margin-bottom: 0;
  display: inline;
}

form .tipo__div {
  align-items: center;
  gap: 20px;
  display: flex;
}

form select {
  width: 100%;
  height: 30px;
}

.tipo__form {
  margin-bottom: 30px;
  padding: 20px;
}

#btn-holder {
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

@media screen and (max-width: 580px) {
  #btn-holder {
    flex-direction: column;
    gap: 20px;
  }
}

.printBtn, .resetBtn {
  height: 49px;
  vertical-align: middle;
  background: #fff;
  padding: 10px;
  font-size: 1.5em;
  transition: all .5s;
}

.printBtn:hover, .resetBtn:hover {
  color: #fff;
  background-color: #000;
}

.material__div {
  border: 1px solid #000;
  padding: 5px;
}

.material__p {
  margin-top: 10px;
  display: block;
}

.materialNumberHolder {
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin-bottom: 30px;
  display: flex;
}

@media screen and (max-width: 580px) {
  .materialNumberHolder {
    flex-direction: column;
  }
}

.materialNumberHolder input {
  margin: 0;
}

.materialNumberHolder p {
  cursor: pointer;
  background: #fff;
  border: 1px solid;
  padding: 10px;
  font-family: inherit;
  font-weight: 800;
  transition: all .3s;
}

.materialNumberHolder p:hover {
  color: #fff;
  background-color: #00c;
  box-shadow: 0 1px 3px;
}

div.table table td, textarea {
  border-color: #000;
}

.table {
  background-color: #fff;
  display: none;
}

.flex-holder__table {
  grid-template-columns: 0fr 71%;
  display: grid;
}

.flex-holder__table .logo__table {
  height: 55px;
}

table, td, tr, th {
  border: 1px solid;
  padding: 3px;
}

table {
  border-collapse: collapse;
  width: 90vw;
  margin: 2px;
}

table td {
  text-align: center;
  height: 30px;
  font-size: 10pt;
}

.table__header--info {
  height: 50px;
}

.textTable {
  width: 99%;
  box-sizing: border-box;
  resize: none;
  border: none;
  outline: none;
  margin-left: 1px;
  padding: 2px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 13.3px;
}

.textarea__td {
  text-align: center;
  padding: 0 1px 0 0;
}

th.group-title, th {
  background-color: #c4c4c4;
  font-weight: 500;
}

thead td {
  vertical-align: middle;
  height: 10px;
}

#iTableType {
  margin-left: 10px;
}

.caption {
  font-size: 15pt;
  font-weight: 700;
}

